@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
.pokemon-card {
    width: 200px;
    background-color: white;
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.pokemon-name {
    font-family: 'Amatic SC', cursive;
    color: #3E3F42;
}

.pokemon-img {
    height: 100px;
    margin: 1rem 0;
}

.abilities-list {
    list-style-type: none;
    padding: 0;
}

.ability-item {
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .5rem;
    margin: .5rem;
    background: rgb(242,189,0);
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.nav {
    display: flex;
    list-style-type: none;
    margin-bottom: 1rem;
}

.nav-item {
    color: black;
    text-decoration: none;
    padding: 10px;
    background: #2872B9;
    margin: 1rem;
    font-size: 18px;
    border: 1px solid black;
    cursor: pointer;
}

button:disabled {
    background: darkgrey;
    cursor: not-allowed;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgb(242,189,0);
}

.logo {
    width: 400px;
    margin: 2rem 0;
}

.pokemon-list {
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 90%;
    margin: auto;
}


